$breakpoints: (
	'phoneSm': 320px,
	'phone': 480px,
	'tabletSm': 650px,
	'tablet': 768px,
	'desktop': 1024px,
	'widescreen': 1200px
);

// keywords
$media-expressions: (
	'screen': 'screen',
	'print': 'print',
	'handheld': 'handheld',
	'landscape': '(orientation: landscape)',
	'portrait': '(orientation: portrait)',
	'retina2x': '(-webkit-min-device-pixel-ratio: 1.5), (min-resolution: 144dpi)',
	'retina3x': '(-webkit-min-device-pixel-ratio: 2.5), (min-resolution: 240dpi)'
);

// ligatured operators ≥ ≤
@import url('https://fonts.googleapis.com/css2?family=Bowlby+One&family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

@import 'vendors/include-media';
@import 'vendors/normalize';
@import 'vendors/slick';

@import 'base/functions';
@import 'base/variables';
@import 'base/mixins';
@import 'base/helpers';
@import 'base/reset';
@import 'base/typography';
@import 'base/forms';

@import 'layouts/header';
@import 'layouts/common';
@import 'layouts/footer';

// section
.intro-section {
	position: relative;
	background: $black;
	padding: 15px 0 0;
	text-align: center;
	overflow: hidden;

	@include media ('>=tablet') {
		padding: 25px 0 0;
	}

	@include media ('>=tablet') {
		padding: 50px 0 0;
	}

	&:before {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%; 
		aspect-ratio: 1920 / 120;
		background: url('../images/decoration-01.svg') no-repeat center bottom;
		background-size: cover; 
		z-index: 2;
	}

	h1, h2, h3, h4, h5, h6 {
		margin: 0;
	}

	.banner-holder {
		margin: 30px -10% 3px;

		@include media ('>=tablet') {
			margin: 0 -10% 3px;
		}

		@include media ('>=1400px') {
			margin: 0 -14.5% 3px;
		}
	}

	.img-holder {
		position: relative;
		margin: 0 -18% 0 -24%;
	}
}

.products-section {
	background: $yellow;
	padding: 30px 0;

	@include media ('>=tablet') {
		padding: 40px 0 50px;
	}

	&.columns-reverse {

		@include media ('>=tablet') {
			padding: 40px 0 70px;
		}

		.columns-block {

			@include media ('>=phone') {
				flex-direction: row;
			}
		}
	}

	.columns-block {

		@include media ('>=phone') {
			display: flex;
			flex-direction: row-reverse;
			justify-content: space-between;
			align-items: center;
			gap: 0 30px;
		}

		@include media ('>=desktop') {
			align-items: flex-start;
		}

		.img-column {

			@include media ('>=phone') {
				width: calc(50% - 15px);
			}

			@include media ('>=tablet') {
				width: calc(61.05% - 15px);
			}

			.img-product {
				display: grid;
				grid-template-columns: 1.75fr 1fr;
				grid-template-rows: auto auto;
				grid-column-gap: 20px;
				grid-row-gap: 20px;
				align-items: end;

				@include media ('>=desktop') {
					grid-template-columns: 1.75fr 1fr;
					grid-template-rows: repeat(2, 1fr);
				}

				.img-01 {
					grid-column: 1 / 2;
					grid-row: 2;
					position: relative;
					background-image: url('../images/ico-01.svg');
					background-size: 100% auto;
					background-position: bottom center;
					background-repeat: no-repeat;

					@include media ('>=desktop') {
						grid-area: 1 / 1 / 3 / 2;
					}

					&:before {
						content: '';
						position: absolute;
						top: 62%;
						right: 3.4%;
						background: url('../images/ico-02.svg') no-repeat center center;
						background-size: cover;
						aspect-ratio: 1;
						width: 26%;
					}

					img {
						max-height: 680px;
						width: auto;
					}
				}

				.text-holder {
					position: relative;
					grid-column: 1 / -1;
					line-height: 1.25;

					@include media ('>=desktop') {
						grid-area: 1 / 2 / 2 / 3;
						padding: 13px;
					}

					&:before {
						@include size(27px, 21px);

						content: '';
						position: absolute;
						top: -6%;
						left: -2%;
						background: url('../images/ico-03.svg') no-repeat center center;
						background-size: cover;

						@include media ('>=desktop') {
							top: 6%;
							left: 2%;
						}
					}
				}

				.img-02 {
					grid-column: 2 / 3;
					grid-row: 2;
					max-width: 230px;
					margin: 0 0 0 -18%;

					@include media ('>=desktop') {
						grid-area: 2 / 2 / 3 / 3;
					}
				}
			}
		}

		.post-column {

			@include media ('>=phone') {
				width: calc(50% - 15px);
			}

			@include media ('>=tablet') {
				width: calc(38.95% - 15px);
				max-width: 340px;
			}

			.circle-text {
				@include size(125px);

				position: relative;
				animation: spin 20s linear infinite;
				border: 1px solid $black;
				border-radius: 50%;
				margin: 30px auto;

				@include media ('>=phone') {
					margin: 0 0 30px;
				}

				@include media ('>=tablet') {
					@include size(198px);

					margin: 50px 0;
				}

				svg {
					@include size(100%);
				}

				text {
					fill: $black;
					font-weight: 400;
					font-size: 15px;
					letter-spacing: .7px;

					.dot {
						fill: $white;
						font-weight: 700;
					}
				}

				.inner-circle {
					fill: none;
					stroke: $black;
					stroke-width: 1;
				}

				@keyframes spin {
					from { transform: rotate(0deg); }
					to { transform: rotate(360deg); }
				}
			}

			span {
				display: block;
			}

			.article-title {
				font-weight: 700;
				font-size: 25px;
				line-height: 1.1;
				margin: 0 0 20px;

				@include media ('>=tablet') {
					font-size: 40px;
				}
			}

			.article-name,
			.price {
				font-weight: 500;
				font-size: 20px;
				line-height: 1.13;
				text-transform: uppercase;
				margin: 0 0 22px;

				@include media ('>=tablet') {
					font-size: 32px;
				}
			}

			.price {
				font-weight: 700;
				color: $yellow;
				text-shadow:
				  -1px -1px 0 black,
				   1px -1px 0 black,
				  -1px  1px 0 black,
				   1px  1px 0 black;
			}

			.size-picker {
				display: flex;
				align-items: center;
				gap: 5px;
				margin: 0 0 30px;

				.label {
					font-weight: 700;
					margin-right: 6px;
				}

				input[type="radio"] {
					display: none;
				}

				label {
					@include size(26px);

					display: flex;
					align-items: center;
					justify-content: center;
					font-weight: 700;
					font-size: 10px;
					text-align: center;
					border: 1px solid #7F7F7F;
					border-radius: 2px;
					cursor: pointer;
					transition: all 0.2s ease;

					&:hover {
						background: $black;
						color: $white;
						border: 1px solid $black;
					}
				}

				input[type="radio"]:checked + label {
					background: $black;
					color: $white;
					border: 1px solid $black;
				}
			}
		}
	}
}

.slider-section {
	background: $yellow;
	padding: 30px 0;
	text-align: center;
	overflow: hidden;

	@include media ('>=tablet') {
		padding: 50px 0 60px;
	}

	.card-slider {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		gap: 0 20px;
		margin: 30px 0 0;

		@include media ('>=tablet') {
			margin: 50px 0 0;
		}

		@include media ('>=desktop') {
			margin: 80px 0 0;
		}
	}

	.slick-list {
		margin: 0 -10px 22px;
		padding: 0 0 8px;
		order: 1;

		@include media ('>=tablet') {
			margin: 0 -10px 42px;
		}
	}

	.slide {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		height: inherit;
		padding: 30px;
		margin: 0 10px;

		img {
			max-width: 340px;
			max-height: 340px;
		}

		&:hover {

			&:before {
				box-shadow: 8px 8px 0px 0px $black;
			}

			img {
				transform: scale(1.03);
			}
		}

		&:before {
			@include size(100%);
			@include animate(box-shadow);

			content: '';
			position: absolute;
			bottom: 0;
			left: 0;
			background: $gray100;
			border-radius: 20px;
		}

		&:nth-child(even) {

			&:before {
				@include size(100%, 80%);
			}
		}

		a {
			display: block;
		}

		img {
			@include animate(transform);

			position: relative;
		}
	}

	.slick-arrow {
		@include size(50px);
		@include animate(background);

		display: grid;
		place-content: center;
		padding: 10px;
		border: 2px solid $white;
		border-radius: 50%;
		outline: none;
		background: none;
		z-index: 100;
		order: 2;

		@include media ('>=tablet') {
			@include size(70px);
		}

		&:hover {
			background: $white;

			svg path {
				fill: $black;
			}
		}

		svg {
			display: block;
			width: 16px;
			margin-right: .1em;

			path {
				@include animate(fill);

				fill: $white;
			}
		}

		&.arrow-next {
			left: 85px;
			scale: -1;
		}

		&.slick-disabled {
			opacity: .3;

			&:hover {
				background: none;

				svg path {
					fill: $white;
				}
			}
		}
	}
}

.feature-section {
	position: relative;
	background: $black;
	padding-top: 30px;
	padding-bottom: clamp(30px, 8vw, 400px);
	text-align: center;

	@include media ('>=tablet') {
		padding-top: 50px;
	}

	@include media ('>=desktop') {
		padding-top: 100px;
	}

	&:before {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%; 
		aspect-ratio: 1920 / 120;
		background: url('../images/decoration-01.svg') no-repeat center bottom;
		background-size: cover; 
		z-index: 2;
	}

	h1, h2, h3, h4, h5, h6 {
		color: $yellow;
	}

	.container {

		@include media ('>=desktop') {
			max-width: 1365px;
		}
	}

	.img-holder {
		margin: 0 0 30px;
	}
}