//elements
h1, h2, h3, h4, h5, h6 {
	font-weight: 400;
	color: $gray100;
	text-transform: uppercase;
}

h1 {
	font-size: 42px;
	line-height: 1.04;

	@include media ('>=tablet') {
		font-size: 65px;
	}

	@include media ('>=desktop') {
		font-size: 96px;
	}
}

h2 {
	font-size: 38px;
	line-height: 1.05;

	@include media ('>=tablet') {
		font-size: 55px;
	}

	@include media ('>=desktop') {
		font-size: 70px;
	}
}

h3 {
	font-size: 32px;

	@include media ('>=tablet') {
		font-size: 45px;
	}

	@include media ('>=desktop') {
		font-size: 60px;
	}
}

h4 {
	font-size: 22px;
	line-height: 1.1;

	@include media ('>=tablet') {
		font-size: 32px;
	}

	@include media ('>=desktop') {
		font-size: 40px;
	}
}

h5 {
	font-size: 20px;

	@include media ('>=tablet') {
		font-size: 30px;
	}

	@include media ('>=desktop') {
		font-size: 36px;
	}
}

h6 {
	font-size: 18px;
	line-height: 1.25;

	@include media ('>=tablet') {
		font-size: 28px;
	}

	@include media ('>=desktop') {
		font-size: 32px;
	}
}

mark {
	background: none;
	color: $yellow;
}

.container {
	max-width: 1320px;
	padding: 0 20px;
	width: 100%;
	margin: 0 auto;

	@include media ('>=tablet') {
		padding: 0 30px;
	}

	@include media ('>=desktop') {
		padding: 0 40px;
	}
}

.btn {
	@include animate(box-shadow);

	display: inline-block;
	background: $gray100;
	padding: 15px;
	font-weight: 700;
	color: $black;
	text-decoration: none;
	text-align: center;
	border: 2px solid $black;
	box-shadow: 8px 8px 0px 0px $black;

	@include media ('>=tablet') {
		padding: 16px 32px;
	}

	&:hover {
		box-shadow: 0px 0px 0px 0px $black;
	}

	&:before {
		@include size(20px);

		content: '';
		display: inline-block;
		background: url('https://cdn.shopify.com/s/files/1/0923/5057/9072/files/ico-01.svg?v=1760898177') no-repeat center center;
		margin-right: 12px;
		vertical-align: middle;
	}
}

.logo {
	display: block;
	width: 150px;

	@include media ('>=tablet') {
		width: 200px;
	}

	@include media ('>=widescreen') {
		width: 300px;
	}

	a, img {
		display: block;
	}
}