#footer {
	background: $yellow;
	padding: 30px 0;

	@include media ('>=tablet') {
		padding: 50px 0;
	}

	@include media ('>=desktop') {
		padding: 100px 0 50px;
	}

	.columns-block {
		padding: 0 0 30px;
		border-bottom: 1px solid $black;

		@include media ('>=tablet') {
			display: flex;
			justify-content: space-between;
			gap: 0 30px;
			padding: 0 0 63px;
		}

		.logo-holder {
			margin: 0 0 30px;

			@include media ('>=tablet') {
				margin: 0 0 40px;
			}

			.logo {
				margin: 0 auto;

				@include media ('>=tablet') {
					margin: 0;
				}
			}
		}

		.link-list {
			@extend %listreset;

			display: flex;
			justify-content: center;
			flex-wrap: wrap;
			gap: 15px 30px;
			font-weight: 700;
			font-size: 14px;

			@include media ('>=desktop') {
				font-size: 16px;
				gap: 15px 50px;
			}

			a {
				position: relative;
				text-decoration: none;

				&:after {
					@include size(0%, 2px);
					@include animate(width);

					content: '';
					display: block;
					background: $black;
				}

				&:hover {

					&:after {
						@include size(100%, 2px);
					}
				}
			}
		}

		.social-list {
			@extend %listreset;

			display: flex;
			justify-content: center;
			align-items: center;
			gap: 0 30px;
			margin: 30px 0 0;

			@include media ('>=desktop') {
				margin: 40px 0 0;
				gap: 0 35px;
			}

			a {
				@include animate(opacity);

				display: block;

				&:hover {
					opacity: .7;
				}
			}

			img {
				width: 20px;

				@include media ('>=tablet') {
					width: 30px;
				}

				@include media ('>=desktop') {
					width: 40px;
				}
			}
		}
	}

	.copy-right {
		padding: 30px 0 0;
		font-weight: 700;
		text-align: center;
		font-size: 14px;

		@include media ('>=tablet') {
			padding: 50px 0 0;
			font-size: 16px;
		}
	}
}